import routesComponent from './routesComponent'

export default [

    {
    //注册
    path: '/register',
    name: 'register',
    component: routesComponent.RegisterComponent
    },
    {
    //登录
    path: '/enroll',
    name: 'enroll',
    component: routesComponent.EnrollComponent
    },
    {
    //显示页
    path: '/showpage',
    name: 'showpage',
    component: routesComponent.ShowpageComponent
    },
    //阅读页
    {
    path: '/read',
    name: 'read',
    component: routesComponent.ReadComponent
    },
    {
    //首页
    path: '/index',
    name: 'index',
    component: routesComponent.IndexComponent,

    children:[
            {
                path: '/bookshelf',
                name: 'bookshelf',
                component: routesComponent.BookshelfComponent
            },
            {
                path: '/sort',
                name: 'sort',
                component: routesComponent.SortComponent
            },
            {
                path: '/ranki',
                name: 'ranki',
                component: routesComponent.RankiComponent
            },
            {
                path: '/searchs',
                name: 'searchs',
                component: routesComponent.SearchsComponent
            }
        ]
    },

    {
    path: '*',
    redirect: {name: 'enroll'}
    }

]