import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'


import axios from 'axios'

import VueAxios from 'vue-axios'

import { Button,
         Icon,
         Uploader ,
         Field,
         CellGroup,
         Tabbar,
         TabbarItem,
         Search,
         Pagination,
         Toast,
        }
      from 'vant'

import 'lib-flexible/flexible'

Vue.use(VueAxios, axios)

Vue
  .use(Button)
  .use(Icon)
  .use(Uploader)
  .use(Field)
  .use(CellGroup)
  .use(Toast)
  .use(Tabbar)
  .use(TabbarItem)
  .use(Search)
  .use(Pagination)

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
